import React, { useContext, useState } from "react";
import translationService from "../../../services/translation.service";
import '../Cart.css';
import { Helmet } from 'react-helmet';
import { ReactComponent as BackIcon } from '../../../images/leftIcon.svg';
import { useNavigate } from "react-router";
import { FadeIn } from "react-slide-fade-in";
import useWindowSize from "../../../hooks/UseWindowSize";
import { ReactComponent as UserIcon } from '../../../images/user.svg'
import { UserContext } from "../../../context/user-context";
import toastrService from "../../../services/toastr.service";
import { getRoute } from "../../../services/routes.service";
import { Ad } from "../Ad";

const UserData = () => {
  const {userData, handleInputChange, onCalcSymbolsOnlyChange, setUserData} = useContext(UserContext);
  const [isFormSubmitted, setIsFormSubmitted] = useState(false);
  
  var navigate = useNavigate();
  var size = useWindowSize();
  const isMobile = size.width < 620

  const handleNextStep = async () => {
    if (!userData.name
      || !userData.phone
      || !userData.email) {
      setUserData((prevOrder) => ({
        ...prevOrder,
        name: prevOrder.name || "",
        phone: prevOrder.phone || "",
        email: prevOrder.email || "",
      }));

      setIsFormSubmitted(true);
      toastrService.callToastr("Будь ласка заповніть вказане поле");
      return;
    }
    else{
      toastrService.callToastr("Дані збережено");
      navigate(getRoute(`shipping_info`))
    }
  };

  return (
    <div className="grid md:grid-cols-2 grid-cols-1 mt-[50px]">
      <FadeIn
        from="bottom"
        positionOffset={700}
        triggerOffset={200}
        delayInMilliseconds={-550}
        durationInMilliseconds={850}
      >
          <Helmet>
            <title>Введіть ваші персональні дані | TVOI</title>
          </Helmet>
          <div className="p-3">
            <div className="w-full flex justify-center">
              <div className="step-style text-secondary flex items-center cursor-pointer" onClick={() => navigate(-1)}>
                <BackIcon className="w-[30px] pr-2"/>
                <div>Крок 1/2</div>
              </div>
            </div>

            <div className="order-element">

              <div className="rowStyle">
                <span><UserIcon/></span>
                  <span className="component-fill">
                    <label className="inputStyle">&nbsp;&nbsp;&nbsp;&nbsp;Введіть ваше Прізвище та Ім'я:*</label>
                    <input
                      type="text"
                      name="name"
                      placeholder="Напр.: Іван Іваненко"
                      className={`form-control ${isFormSubmitted && !userData.name ? "error" : ""}`}
                      value={userData.name}
                      onChange={handleInputChange}
                    />
                  </span>
              </div>

              <label className="inputStyle">&nbsp;&nbsp;&nbsp;&nbsp;{translationService.translate("phone|A")}:*</label>
              <input
                type="text"
                name="phone"
                placeholder="+380"
                className={`form-control ${isFormSubmitted && !userData.phone ? "error" : ""}`}
                value={userData.phone}
                onKeyPress={onCalcSymbolsOnlyChange}
                onChange={handleInputChange}
              />
            </div>

            <div className="order-element">
              <label className="inputStyle">&nbsp;&nbsp;&nbsp;&nbsp;Введіть вашу електронну пошту:*</label>
              <input
                type="text"
                name="email"
                placeholder="example@gmail.com"
                className={`form-control ${isFormSubmitted && !userData.email ? "error" : ""}`}
                value={userData.email}
                onChange={handleInputChange}
              />
              <span className="inputStyle mt-2">
                Маєте запитання? 
                <a href="https://www.instagram.com/love.tvoi/?igshid=YmMyMTA2M2Y%3D"> Напишіть нам сюди</a>
              </span>
            </div>

            <div className="confirm-footer">
              <div className="w-100">
                <button className="btnConfirmCartStyle" onClick={handleNextStep}>
                  Продовжити
                </button>
              </div>
            </div>
          </div>
      </FadeIn>
      {!isMobile ? <div className='ad-pos'>
        <Ad title='Хе-хе, а що тут у нас... Лиш дай знати цим лапкам, куди слід відносити посилки!' image='animals/cats_feet.png'/>
      </div> : <></>}
    </div>
  );
};

export default UserData;